<template>
  <div>
    <h1>Cart</h1>
    <p>You have {{ count }} items in your cart</p>
    <h1 v-show='count == 0'> :( </h1>
    <CartList @removeItem="removeItem" :cart="cart" />
    <div>
      <hr />
      <h1>total:</h1>
      <p>{{ total }}</p>
    </div>
  </div>
</template>

<script>
import CartList from "../components/CartList.vue";

export default {
  name: "CartView",
  components: {
    CartList,
  },
  computed: {
    cart() {
      return this.$root.$data.cart;
    },
    total() {
      let sum = 0.0;
      this.$root.$data.cart.forEach((product) => {
        let price = product.price;
        sum += parseFloat(price.slice(1)) * (product.quantity || 1);
      });
      return `$${sum.toFixed(2)}`;
    },
    count() {
      return this.$root.$data.cart.length;
    },
  },
  methods: {
    removeItem(product) {
      console.log("testing");
      let equalID = function (curr) {
        console.log(curr.id);
        console.log(product.id);
        console.log(curr.id != product.id);
        return curr.id != product.id;
      };
      this.$root.$data.cart = this.$root.$data.cart.filter(equalID);
    },
  },
};
</script>