<template>
  <div class="wrapper">
    <div class="products">
      <div class="product" v-for="product in cart" :key="product.id">
        <div class="info">
          <h1>{{ product.name }}</h1>
          <p>Quantity: {{ product.quantity || 1}}</p>
          <p>Origin: {{ product.country }}</p>
        </div>
        <div class="price">
          <h2> {{ product.price }}</h2>
          <button  class="auto" @click="removeItem(product)" >Remove item from cart</button>
        </div>
      </div>
 
    </div>
  </div>
</template>

<script>
export default {
  name: "CartList",
  props: {
    cart: Array,
  },
  methods: {
    removeItem: function(product) {
        console.log(product.id);
        this.$emit('removeItem', product);
    },
  },
};
</script>


